import React, { Fragment } from 'camunda-modeler-plugin-helpers/react';
import { Modal, Fill } from 'camunda-modeler-plugin-helpers/components';
import CandidateUserModal from './modal/CandidateUserModal';
import TipdocModal from './modal/TipdocModal';
import CandidateGroupsModal from './modal/CandidateGroupsModal';
import LoginModal from './modal/LoginModal';
import ProcessModal from './modal/ProcessModal';
import ConfigSei from './modal/ConfigSei';

const STR_ID = 'request-plugin-';

const TYPES = ["bpmn:UserTask", "bpmn:Process", "bpmn:Collaboration"]

const TIP_DOC_REG = /(Activity_([a-zA-Z]+)_(\d*))/i;
const TIP_PROC_REG = /(SEI_(\d*))/i;

// const API_ROOT = 'https://unb-sei.cade.gov.br/sei/modulos/api-modeler/controlador_externo.php?acao=';
const API_ROOT = '/modulos/api-modeler/controlador_externo.php?acao=';
const API_LIST_DOC = `${API_ROOT}sistema_tipodocumento`;
const API_LIST_USERS = `${API_ROOT}sistema_usuario`;
const API_LIST_GROUPS = `${API_ROOT}sistema_cargo`;
const API_PROCESS = `${API_ROOT}sistema_tipoprocesso`;
const API_LOGIN = `${API_ROOT}token_gettoken`;
const LoadingIcon = 'data:image/svg+xml;base64,';
const SeiIcon = 'data:image/png;base64,';

const MODAL_TYPE = {
  listDoc: 0,
  listUsers: 1,
  listGroups: 2,
  listAssignee: 3,
  login: 4,
  listProc: 5,
  configSei: 6,
};

function createSVGIcon() {
  const svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');

  svg.setAttribute('viewBox', '0 0 512 512');
  svg.setAttribute('version', '1.1');
  svg.setAttribute('width', '12');
  svg.setAttribute('height', '16');
  svg.setAttribute('aria-hidden', 'true');

  const path = document.createElementNS('http://www.w3.org/2000/svg', 'path');
  path.setAttribute('fill-rule', 'evenodd');
  path.setAttribute('d',
    'M445,386.7l-84.8-85.9c13.8-24.1,21-50.9,21-77.9c0-87.6-71.2-158.9-158.6-158.9C135.2,64,64,135.3,64,222.9  c0,87.6,71.2,158.9,158.6,158.9c27.9,0,55.5-7.7,80.1-22.4l84.4,85.6c1.9,1.9,4.6,3.1,7.3,3.1c2.7,0,5.4-1.1,7.3-3.1l43.3-43.8  C449,397.1,449,390.7,445,386.7z M222.6,125.9c53.4,0,96.8,43.5,96.8,97c0,53.5-43.4,97-96.8,97c-53.4,0-96.8-43.5-96.8-97  C125.8,169.4,169.2,125.9,222.6,125.9z');

  svg.appendChild(path);

  return svg;
}

async function requestListaTipoProcessos() {
  const userData = JSON.parse(window.localStorage.getItem('userData'));
  const response = await fetch(window.localStorage.getItem('urlSei')+API_PROCESS, {
    method: 'GET',
    headers: new Headers({
      'Token': `Bearer ${userData.dados.token}`,
    })
  });

  const data = await response.json();
  return data;
}

async function requestListaTipoDocumento() {
  const userData = JSON.parse(window.localStorage.getItem('userData'));
  const response = await fetch(window.localStorage.getItem('urlSei')+API_LIST_DOC, {
    method: 'GET',
    headers: new Headers({
      'Token': `Bearer ${userData.dados.token}`,
    })
  });

  const data = await response.json();
  return data;
}

async function requestListaCandidateUsers() {
  const userData = JSON.parse(window.localStorage.getItem('userData'));
  const response = await fetch(window.localStorage.getItem('urlSei')+API_LIST_USERS, {
    method: 'GET',
    headers: new Headers({
      'Token': `Bearer ${userData.dados.token}`,
    })
  });

  const data = await response.json();
  return data;
}

async function requestListaCandidateGroups() {
  const userData = JSON.parse(window.localStorage.getItem('userData'));
  const response = await fetch(window.localStorage.getItem('urlSei')+API_LIST_GROUPS, {
    method: 'GET',
    headers: new Headers({
      'Token': `Bearer ${userData.dados.token}`,
    })
  });
  const data = await response.json();
  return data;
}

function buildSearchButton() {
  const button = document.createElement('button');
  button.classList.add('action-button');
  button.attributes['data-action'] = 'open-github-search';
  button.classList.add('open-search');
  button.appendChild(createSVGIcon());

  button.style.backgroundColor = 'transparent';
  button.style.border = 'none';
  button.style.right = '24px';
  button.style.display = 'flex';
  button.style.cursor = 'pointer';

  return button;
}

function buildTipoDocInfo() {
  const div = document.createElement("div");
  div.classList.add("tipo-doc-info");
  return div;
}

export default class RequestListPlugin extends React.PureComponent {
  constructor(props) {
    super(props);

    this.loadingIcon = LoadingIcon;
    this.state = {
      showModal: false,
      data: [],
      filter: '',
      filteredData: [],
      processData: [],
      filteredAssignee: [],
      selectedUsers: [],
      user: '',
      pwd: '',
      userData: '',
      userIsLogged: false,
      loading: false,
    };

    this.openModal = this.openModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.setFilter = this.setFilter.bind(this);
    this.setIdContent = this.setIdContent.bind(this);
    this.setIdProcess = this.setIdProcess.bind(this);
    this.selectContent = this.selectContent.bind(this);
    this.selectProcess = this.selectProcess.bind(this);
    this.selectAssignee = this.selectAssignee.bind(this);

    this.setCandidateUser = this.setCandidateUser.bind(this);
    this.selectCandidateUser = this.selectCandidateUser.bind(this);
    this.setCandidateUserFilter = this.setCandidateUserFilter.bind(this);
    this.setUser = this.setUser.bind(this);
    this.setPwd = this.setPwd.bind(this);
    this.setUrlSei = this.setUrlSei.bind(this);
    this.checkUser = this.checkUser.bind(this);
    this.sendConfig = this.sendConfig.bind(this);

    this.setCandidateGroup = this.setCandidateGroup.bind(this);
    this.selectCandidateGroup = this.selectCandidateGroup.bind(this);
    this.setCandidateGroupFilter = this.setCandidateGroupFilter.bind(this);
    this.sendPassword = this.sendPassword.bind(this);
    this.onSelectType = this.onSelectType.bind(this);

    this.tipoDocDiv = null;
  }

  async updateProcessTypeName() {

    const camundaId = document.querySelector('#camunda-id');
    const value = camundaId.value;

    const match = TIP_PROC_REG.exec(value);

    if (match) {
      const number = match[2];
      let name = localStorage.getItem(number);
      if (!name) {
        const data = await requestListaTipoProcessos();

        const process = data.dados.find(doc => doc.id == number);
        name = process.titulo;
      }

      this.updateTipoDocInfo(number, name || "[Desconhecido]");
    }
  }

  async updateDocTypeName() {

    const camundaId = document.querySelector('#camunda-id');
    const value = camundaId.value;

    const match = TIP_DOC_REG.exec(value);

    if (match) {
      const number = match[3];
      let name = localStorage.getItem(number);
      if (!name) {
        const data = await requestListaTipoProcessos();

        const tipoDoc = data.find(doc => doc.id == number);
        name = tipoDoc.titulo;
      }

      this.updateTipoDocInfo(number, name || "[Desconhecido]");
    }
  }

  updateTipoDocInfo(id, value) {
    localStorage.setItem(String(id), value);
    this.tipoDocDiv.innerHTML = `${id} - ${value}`;
  }

  createTipoDocInfo() {
    const parent = document.querySelector("label[for=camunda-id]").parentElement;
    const elementId = `${STR_ID}tipodoc-div`;

    if (!parent.querySelector(`#${elementId}`)) {
      this.tipoDocDiv = buildTipoDocInfo();
      this.tipoDocDiv.id = elementId;
      parent.append(this.tipoDocDiv);
    }
  }

  createSearchButton() {
    const button = buildSearchButton();
    button.id = `${STR_ID}search-button`;

    const camundaId = document.querySelector('#camunda-id');
    if (!camundaId.querySelector(`#${button.id}`)) { // Previne que sejam adicionados elementos que já existem 
      camundaId.classList.add('with-search');
      camundaId.parentElement.appendChild(button);
      button.addEventListener('click', () => {
        this.openModal(MODAL_TYPE.listDoc);
      });
    }
  }

  createProcessSearchButton() {
    const button = buildSearchButton();
    button.id = `${STR_ID}search-button`;

    const camundaId = document.querySelector('#camunda-id');
    if (!camundaId.querySelector(`#${button.id}`)) { // Previne que sejam adicionados elementos que já existem
      camundaId.classList.add('with-search');
      camundaId.parentElement.appendChild(button);
      button.addEventListener('click', () => {
        this.openModal(MODAL_TYPE.listProc);
      });
    }
  }

  createCandidateUserSearchButton() {
    const button = buildSearchButton();
    button.id = `${STR_ID}-candidate-users-search-button`;

    const camundaId = document.querySelector('#camunda-candidateUsers');
    if (camundaId && !camundaId.querySelector(`#${button.id}`)) { // Previne que sejam adicionados elementos que já existem 
      camundaId.classList.add('with-search');
      camundaId.parentElement.appendChild(button);
      button.addEventListener('click', () => {
        this.openModal(MODAL_TYPE.listUsers);
      });
    }
  }

  createLoginSearchButton() {
    const button = buildSearchButton();
    button.id = `${STR_ID}-login-search-button`;
    const camundaId = document.querySelector('#camunda-candidateStarterUsers');
    if (camundaId && !camundaId.querySelector(`#${button.id}`)) { // Previne que sejam adicionados elementos que já existem 
      camundaId.classList.add('with-search');
      camundaId.parentElement.appendChild(button);
      button.addEventListener('click', () => {
        this.openModal(MODAL_TYPE.login);
      });
    }
  }

  createCandidateGroupSearchButton() {
    const button = buildSearchButton();
    button.id = `${STR_ID}-candidate-group-search-button`;

    const camundaId = document.querySelector('#camunda-candidateGroups');
    if (camundaId && !camundaId.querySelector(`#${button.id}`)) { // Previne que sejam adicionados elementos que já existem 
      camundaId.classList.add('with-search');
      camundaId.parentElement.appendChild(button);
      button.addEventListener('click', () => {
        this.openModal(MODAL_TYPE.listGroups)
      });
    }
  }

  createCandidateAssigneeSearchButton() {
    const button = buildSearchButton();
    button.id = `${STR_ID}-assignee-search-button`;

    const camundaId = document.querySelector('#camunda-assignee');
    if (camundaId && !camundaId.querySelector(`#${button.id}`)) { // Previne que sejam adicionados elementos que já existem 
      camundaId.classList.add('with-search');
      camundaId.parentElement.appendChild(button);
      button.addEventListener('click', () => {
        this.openModal(MODAL_TYPE.listAssignee);
      });
    }
  }

  componentDidMount() {
    const { subscribe } = this.props;
    this.checkUser();
    this.checkUrl();

    subscribe('bpmn.modeler.created', (event) => {
      const { modeler } = event;

      modeler.on('propertiesPanel.changed', ({ current: { element } }) => {

        if (TYPES.includes(element.type)) {
          this.createTipoDocInfo();

          if (element.type == 'bpmn:Process') {
            this.createProcessSearchButton();
            this.updateProcessTypeName();
          } else {
            this.createSearchButton();
            this.updateDocTypeName();
          }

          this.createCandidateUserSearchButton();
          this.createCandidateGroupSearchButton();
          this.createCandidateAssigneeSearchButton();
          this.createLoginSearchButton();
        }
      });
    });
  }

  async componentDidUpdate(_, prevState) {
    if (!prevState.showModal && this.state.showModal) {

      let usersData = [];
      let data = [];
      let groupData = [];
      let processData = [];
      let assigneeData = [];

      switch (this.state.modalType) {
        case MODAL_TYPE.listDoc:
          const tipo = await requestListaTipoDocumento();
          data = tipo.dados;
          break;
        case MODAL_TYPE.listProc:
          const processo = await requestListaTipoProcessos();
          processData = processo.dados;
          break;
        case MODAL_TYPE.listUsers:
          const users = await requestListaCandidateUsers();
          usersData = users.dados;
          break;
        case MODAL_TYPE.listGroups:
          const group = await requestListaCandidateGroups();
          groupData = group.dados;
          break;
        case MODAL_TYPE.listAssignee:
          const assignee = await requestListaCandidateUsers();
          assigneeData = assignee.dados;
          break;
      }

      this.setState({
        data,
        filteredData: data,
        usersData,
        filteredUsers: usersData,
        groupData,
        filteredGroups: groupData,
        processData,
        filteredAssignee: assigneeData,
      });
    }
  }

  checkUser() {
    const currentTime = new Date().getTime();
    const storageUser = window.localStorage.getItem('userData');
    const user = window.localStorage.getItem('user');
    const userData = storageUser ? JSON.parse(window.localStorage.getItem('userData')) : '';
    const userExpire = userData ? userData.dados.expire + '000' : 0;

    if (Number(currentTime) > Number(userExpire)) {
      this.setState({ userIsLogged: false });
      window.localStorage.setItem('userData', '');
      return;
    }

    this.setState({ userIsLogged: true, user });
    return userData;
  }

  checkUrl() {
    const url = window.localStorage.getItem('urlSei');
    this.setState({url});
    return url;
  }

  openModal(modalType) {
    const checkUser = this.checkUser();
    const checkUrl = this.checkUrl();

    if (!checkUrl && modalType !== MODAL_TYPE.configSei) {
      this.setState({ afterLogin: modalType });
      this.openModal(MODAL_TYPE.configSei);
      return;
    }


    if (!checkUser && (modalType !== MODAL_TYPE.login && modalType !== MODAL_TYPE.configSei)) {
      this.setState({ afterLogin: modalType });
      this.openModal(MODAL_TYPE.login);
      return;
    }

    this.setState({
      showModal: true,
      modalType,
    });
  }

  closeModal() {
    this.setState({
      showModal: false,
      modalType: null
    });
  }

  setFilter(e) {
    const filter = e.target.value;
    const { data } = this.state;

    let filteredData = data;

    if (filter) {
      filteredData = data.filter(d => d.nome.toLowerCase().includes(filter.toLowerCase()));
    }

    this.setState({ filteredData });
  }

  setIdContent(id, value) {
    const { documentType } = this.state;
    this.setState({ documentType: '' });
    const camundaId = document.querySelector('#camunda-id');
    camundaId.value = `Activity_${documentType}_${id}`.replace('/', '_');

    this.updateTipoDocInfo(id, value);

    const e = new Event('change', {
      'bubbles': true,
      'cancelable': true
    });
    camundaId.dispatchEvent(e);
  }

  setIdProcess(id, value) {
    const camundaId = document.querySelector('#camunda-id');
    camundaId.value = `SEI_${id}`.replace('/', '_');

    this.updateTipoDocInfo(id, value);

    const e = new Event('change', {
      'bubbles': true,
      'cancelable': true
    });
    camundaId.dispatchEvent(e);
  }

  selectContent(e) {
    const target = e.target;
    if (target.classList.contains('btn-select-repo')) {
      const parent = target.parentElement;
      this.setIdContent(parent.dataset['id'], parent.dataset['value']);
      this.closeModal();
    }
  }

  selectProcess(e) {
    const target = e.target;
    if (target.classList.contains('btn-select-repo')) {
      const parent = target.parentElement;
      this.setIdProcess(parent.dataset['id'], parent.dataset['value']);
      this.closeModal();
    }
  }

  setAssignee(value) {
    const assignee = document.querySelector('#camunda-assignee');
    assignee.value = value;

    const e = new Event('change', {
      'bubbles': true,
      'cancelable': true
    });

    assignee.dispatchEvent(e);
  }

  selectAssignee(e) {
    const target = e.target;
    if (target.classList.contains('btn-select-repo')) {
      const parent = target.parentElement;
      this.setAssignee(parent.dataset['value']);
      this.closeModal();
    }
  }

  selectCandidateUser(e) {
    const target = e.target;
    if (target.classList.contains('btn-select-repo')) {
      const parent = target.parentElement;
      this.setCandidateUser(parent.dataset['id'], parent.dataset['value']);
      this.closeModal();
    }
  }

  setCandidateUser(id, value) {
    const candidateUser = document.querySelector('#camunda-candidateUsers');
    const { selectedUsers = [] } = this.state;
    const hasUser = selectedUsers.find(user => user === value);

    if (!hasUser) {
      selectedUsers.push(value);
    } else {
      const index = selectedUsers.indexOf(value);
      if (index !== -1) {
        selectedUsers.splice(index, 1);
      }
    }

    this.setState({ selectedUsers });
    candidateUser.value = selectedUsers.join(', ');

    const e = new Event('change', {
      'bubbles': true,
      'cancelable': true
    });

    candidateUser.dispatchEvent(e);
  }

  setCandidateUserFilter(e) {
    const filter = e.target.value;
    const { usersData } = this.state;

    let filteredUsers = usersData;

    if (filter) {
      filteredUsers = usersData.filter(d => d.nome.toLowerCase().includes(filter.toLowerCase()));
    }

    this.setState({ filteredUsers });
  }

  setCandidateGroup(id, value) {
    const candidateGroup = document.querySelector('#camunda-candidateGroups');

    const { selectedGroup = [] } = this.state;
    const hasUser = selectedGroup.find(user => user === value);

    if (!hasUser) {
      selectedGroup.push(value);
    } else {
      const index = selectedGroup.indexOf(value);
      if (index !== -1) {
        selectedGroup.splice(index, 1);
      }
    }

    this.setState({ selectedGroup });
    candidateGroup.value = selectedGroup.join(', ');

    const e = new Event('change', {
      'bubbles': true,
      'cancelable': true
    });

    candidateGroup.dispatchEvent(e);
  }

  selectCandidateGroup(e) {
    const target = e.target;
    if (target.classList.contains('btn-select-repo')) {
      const parent = target.parentElement;
      this.setCandidateGroup(parent.dataset['id'], parent.dataset['value']);
      this.closeModal();
    }
  }

  setCandidateGroupFilter(e) {
    const filter = e.target.value;
    const { groupsData } = this.state;

    let filteredGroups = groupsData;

    if (filter) {
      filteredGroups = groupsData.filter(d => d.nome.toLowerCase().includes(filter.toLowerCase()));
    }

    this.setState({ filteredGroups });
  }

  onSelectType(type) {
    this.setState({ documentType: type });
  }

  renderTipDocModal(data) {
    const { filteredData = [] } = this.state;

    return <TipdocModal
      docs={filteredData}
      setFilter={this.setFilter}
      setSelected={this.selectContent}
      onClose={this.closeModal}
      onSelectType={this.onSelectType}
      documentTypeIsSelect={this.state.documentType}
    />;
  }

  renderProcessModal(data) {
    const { processData = [] } = this.state;

    return <ProcessModal
      docs={processData}
      setFilter={this.setFilter}
      setSelected={this.selectProcess}
      onClose={this.closeModal}
    />
  }

  renderConfigSeiModal() {
    return <ConfigSei
      sendConfig={this.sendConfig}
      setUrlSei={this.setUrlSei}
      url={this.state.url}
      onClose={this.closeModal}
    />
  }

  renderCandidateUserModal() {
    const { filteredUsers = [], selectedUsers = [] } = this.state;

    return <CandidateUserModal
      users={filteredUsers}
      setFilter={this.setCandidateUserFilter}
      setCandidateUser={this.selectCandidateUser}
      onClose={this.closeModal}
      selectedUsers={selectedUsers}
    />
  }

  renderAssigneeModal() {
    const { filteredAssignee = [] } = this.state;
    const selectedUsers = [];

    return <CandidateUserModal
      users={filteredAssignee}
      setFilter={this.setCandidateUserFilter}
      setCandidateUser={this.selectAssignee}
      onClose={this.closeModal}
      selectedUsers={ selectedUsers }
    />
  }

  renderCandidateGroupModal() {
    const { filteredGroups = [], selectedGroup = [] } = this.state;

    return <CandidateGroupsModal
      groups={filteredGroups}
      setFilter={this.setCandidateGroupFilter}
      setCandidateGroup={this.selectCandidateGroup}
      onClose={this.closeModal}
      selectedGroup={selectedGroup}
    />
  }

  async sendPassword() {
    this.setState({ loading: true });
    let formData = new FormData();

    const pwd = this.state.pwd;
    const arrayPwd = pwd.toString().split('');
    const encondedPwd = arrayPwd.map(pwd => (pwd.charCodeAt() + 1) * -1).join('*');

    formData.append('sigla', this.state.user);
    formData.append('password', btoa(encondedPwd));

    const response = await fetch(
      window.localStorage.getItem('urlSei')+API_LOGIN,
      {
        body: formData,
        method: 'POST'
      });
    const userData = await response.json();
    this.setState({ loading: false });

    if (userData && userData.status) {
      this.setState({ userData, userIsLogged: true });
      window.localStorage.setItem('userData', JSON.stringify(userData));
      window.localStorage.setItem('user', this.state.user);
    } else {
      alert('Dados inválidos');
      return;
    }

    if (this.state.afterLogin) {
      this.closeModal();
      this.openNextModal();
    } else {
      this.closeModal();
    }
  }

  openNextModal() {
    this.openModal(this.state.afterLogin);
    this.setState({ afterLogin: '' });
  }

  setUser(user) {
    this.setState({ user: user.target.value });
  }

  setPwd(pwd) {
    this.setState({ pwd: pwd.target.value });
  }

  setUrlSei(url) {
    this.setState({ url: url.target.value });
  }

  sendConfig() {
    window.localStorage.setItem('urlSei', this.state.url);

    if (this.state.afterLogin) {
      this.closeModal();
      this.openNextModal();
    } else {
      this.closeModal();
    }
  }

  logout() {
    this.setState({ userData: '', userIsLogged: false });
    window.localStorage.setItem('userData', '');
  }

  renderModalLogin() {
    return <LoginModal
      onClose={this.closeModal}
      sendPassword={this.sendPassword}
      setUser={this.setUser}
      setPwd={this.setPwd}
      loading={this.state.loading}
      iconLoading={this.loadingIcon}
    />;
  }

  renderModal() {
    const { modalType } = this.state;
    switch (modalType) {
      case MODAL_TYPE.listDoc:
        return this.renderTipDocModal();
      case MODAL_TYPE.listUsers:
        return this.renderCandidateUserModal();
      case MODAL_TYPE.listGroups:
        return this.renderCandidateGroupModal();
      case MODAL_TYPE.listAssignee:
        return this.renderAssigneeModal();
      case MODAL_TYPE.login:
        return this.renderModalLogin();
      case MODAL_TYPE.listProc:
        return this.renderProcessModal();
      case MODAL_TYPE.configSei:
        return this.renderConfigSeiModal();
    }
  }

  render() {
    const { showModal } = this.state;
    return (
      <Fragment>
        <Fill slot="status-bar__file">
          <a onClick={() => this.openModal(MODAL_TYPE.configSei)}>
            Configuração SEI!
          </a>
        </Fill>
        <Fill slot="toolbar">
          {
            !this.state.userIsLogged &&
            <a onClick={() => this.openModal(MODAL_TYPE.login)}><img src={SeiIcon} width="18" /> </a>
          }
          {
            this.state.userIsLogged &&
            <a onClick={() => this.logout()}><img src={SeiIcon} width="18" /> {this.state.user}, Sair </a>
          }
        </Fill>
        {
          showModal &&
          <Modal onClose={this.closeModal}>
            {this.renderModal()}
          </Modal>}
      </Fragment>);
  }
}